.navbar {
  height: 236px;
  width: 100%;
  font-family: 'Dosis', sans-serif; }
  .navbar .strip {
    width: 100%;
    height: 5px;
    background-color: #FBD133;
    background-image: -webkit-gradient(linear, 0% 50%, 100% 50%, color-stop(0%, #FBD133), color-stop(35%, #F44336), color-stop(65%, #0592BF), color-stop(100%, #2f4f4f)); }
  .navbar .banner {
    padding-top: 58px;
    text-align: center; }
    .navbar .banner #logo {
      width: 365px; }
  .navbar a {
    text-decoration: none;
